import PropTypes from "prop-types"
import React from "react"

const HeroBanner = ({ bannerType, bannerTitle, bannerText, bannerLinkText, bannerLinkURL }) => (
    <div className={"cm-hero-banner " + bannerType}>
        <div className="hero-banner-content">

            <h1>{bannerTitle}</h1>
            {bannerText ? <p>{bannerText}</p> : ""}

            {/* Link is only for homepage full height banners due to height constraints */}
            {
                bannerType === "home-page-banner" ?  
                    bannerLinkText && bannerLinkURL ? 
                        <a href= {bannerLinkURL} className='cta cta-primary'>{bannerLinkText}</a> 
                    : ""
                : '' 
            }
        
        </div>

        {/* Animation arrow is only for homepage full height banners due to height constraints */}
        {
            bannerType === "home-page-banner" ?  
                <div className="hero-banner-next">
                    <a href="#main-content" className="vsa-block-link">Link to Content</a>
                </div>
            : '' 
        }

    </div>
)

HeroBanner.propTypes = {
    bannerType: PropTypes.string,
    bannerTitle: PropTypes.string,
    bannerText: PropTypes.string,
    bannerLinkText: PropTypes.string,
    bannerLinkURL: PropTypes.string,
}

HeroBanner.defaultProps = {
    bannerType: 'content-page-banner',
    bannerTitle: `Title Missing`,
    bannerText: ``,
    bannerLinkText: ``,
    bannerLinkURL: ``,
}

export default HeroBanner;
